import { Fragment } from "react";
import { Menu, Transition } from "@headlessui/react";
import {
  DotsHorizontalIcon,
  PencilAltIcon,
  TrashIcon,
} from "@heroicons/react/outline";

export default function Dropdown({ onDelete, onEdit }) {
  return (
    <Menu as="div" className="relative inline-block text-left">
      <div>
        <Menu.Button className="h-7 w-7">
          <DotsHorizontalIcon aria-hidden="true" />
        </Menu.Button>
      </div>

      <Transition
        as={Fragment}
        enter="transition ease-out duration-100"
        enterFrom="transform opacity-0 scale-95"
        enterTo="transform opacity-100 scale-100"
        leave="transition ease-in duration-75"
        leaveFrom="transform opacity-100 scale-100"
        leaveTo="transform opacity-0 scale-95"
      >
        <Menu.Items className="origin-top-right absolute right-0 -mt-1 rounded-md shadow-lg bg-white ring-1 ring-black ring-opacity-5 focus:outline-none">
          <div className="px-1 pt-1">
            <Menu.Item>
              {({ active }) => (
                <button className="inline-flex" onClick={onEdit}>
                  <PencilAltIcon className=" h-6 text-indigo-500" />
                </button>
              )}
            </Menu.Item>
            <Menu.Item>
              {({ active }) => (
                <button className="inline-flex" onClick={onDelete}>
                  <TrashIcon className=" h-6 text-red-500" />
                </button>
              )}
            </Menu.Item>
          </div>
        </Menu.Items>
      </Transition>
    </Menu>
  );
}
