import { useContext, useState } from "react";
import Pagination from "../Pagination";
import OperationItem from "../OperationItem";
import HeaderTable from "../HeaderTable";
import TableLayout from "../TableLayout";
import { QueriesContext } from "../../QueriesContext";
import IncomeIcon from "../../assets/output-onlinepngtools (1).png";
import ExpenseIcon from "../../assets/output-onlinepngtools.png";
import Dropdown from "../DropDown";
import ModalWindow from "../ModalWindow";

const OperationsList = ({ ...props }) => {
  const { operationList, onDelete, onEdit, cantOperations } = props;

  const [toggleSort, setToggleSort] = useState({
    concept: false,
    category: false,
    amount: false,
    type: false,
    date: false,
  });
  const { setHistorySearchParams, querys } = useContext(QueriesContext);

  const handleToggleSort = (e) => {
    e.preventDefault();
    const name = e.currentTarget.getAttribute("name") || "concept";
    let sortString = "";
    toggleSort[name]
      ? (sortString = name + ":ASC")
      : (sortString = name + ":DESC");
    querys.get("sort")
      ? querys.get("sort").includes(name)
        ? querys.set("sort", querys.get("sort").split(name)[0] + sortString)
        : querys.set("sort", querys.get("sort") + "," + sortString)
      : querys.set("sort", sortString);
    toggleSort[name] = !toggleSort[name];
    setToggleSort(toggleSort);
    setHistorySearchParams(querys);
  };

  const handleResetSort = (e) => {
    let name = e.currentTarget.getAttribute("name") || "concept";
    let sort = querys.get("sort");
    let regex = new RegExp(`,*${name}:ASC,*|,*${name}:DESC,*`, "i");
    !sort?.split(",")[0]?.search(regex) ||
    !sort?.split(",")[sort?.split(",").length - 1]?.search(regex)
      ? (sort = sort?.replace(regex, ""))
      : (sort = sort?.replace(regex, ","));
    querys.set("sort", sort);
    !sort && querys.delete("sort");
    toggleSort[name] = false;
    setToggleSort(toggleSort);
    setHistorySearchParams(querys);
  };

  return (
    <>
      {operationList?.length > 0 && (
        <>
          <TableLayout>
            <HeaderTable
              handleResetSort={handleResetSort}
              handleToggleSort={handleToggleSort}
              toggleSort={toggleSort}
            />
            <OperationItem
              operationList={operationList}
              onEdit={onEdit}
              onDelete={onDelete}
            />
          </TableLayout>
          <OperationsCards
            operationList={operationList}
            onEdit={onEdit}
            onDelete={onDelete}
          />
        </>
      )}
      <Pagination cantOperations={cantOperations} />
    </>
  );
};

export default OperationsList;

function OperationsCards({ operationList, onEdit, onDelete }) {
  const [openDeleteModal, setOpenDeleteModal] = useState(false);
  const [id, setId] = useState(null);

  const handleClose = () => {
    setOpenDeleteModal(false);
  };

  const handleDelete = (id) => {
    onDelete(id);
    setOpenDeleteModal(false);
  };

  return (
    <div className=" grid gap-2 sm:hidden text-indigo-800">
      {operationList?.map((operation, count) => (
        <div
          key={operation.id}
          className={`${
            count % 2 === 0 ? "bg-indigo-200 " : "bg-indigo-300 "
          } cursor-pointer rounded-2xl p-2 relative shadow-md`}
        >
          <div className=" absolute right-2 top-1 ">
            <Dropdown
              onEdit={() => onEdit(operation)}
              onDelete={() => {
                setOpenDeleteModal(true);
                setId(operation.id);
              }}
            />
          </div>
          <div className=" flex">
            <div className=" min-h-full max-w-xs grid justify-center items-centen">
              <div className="p-1 rounded-md  w-24">
                {operation["type"] === "Expense" ? (
                  <img
                    src={ExpenseIcon}
                    alt=""
                    className=" text-red-500 h-24"
                  />
                ) : (
                  <img
                    src={IncomeIcon}
                    alt=""
                    className=" text-green-500 h-24"
                  />
                )}
              </div>
            </div>
            <div className=" text-xs ">
              <p className="px-3 sm:px-6">
                <strong>{"Concept: "}</strong>
                {operation.concept}
              </p>
              <p className="px-3 sm:px-6">
                <strong>{"Category: "}</strong>
                {operation.category}
              </p>
              <p className="px-3 sm:px-6">
                <strong>{"Date: "}</strong>
                {operation.date}
              </p>
            </div>
            <div className="flex items-center justify-center text-lg">
              {operation["type"] === "Expense" ? (
                <strong className=" text-red-500">
                  {"-$"}
                  {operation.amount}
                </strong>
              ) : (
                <strong className=" text-green-500">
                  {"+$"}
                  {operation.amount}
                </strong>
              )}
            </div>
          </div>
        </div>
      ))}
      <ModalWindow
        modalText={"Are you sure you want to delete this operation?"}
        open={openDeleteModal}
        handleClose={() => handleClose()}
        handleConfirm={() => handleDelete(id)}
        type={"delete"}
      />
    </div>
  );
}
